@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&family=Roboto:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*{
    box-sizing: border-box;
}
body{
    width:100%;
    background-color: #efefef;
    font-family: 'Roboto', sans-serif;
}

.container{
    max-width: 700px;
    background-color: #fff;
    min-height: 100vh;
    margin: 0 auto;
}
header{
    width:100%;
    background-color: #424242;
    color:#fff;
    padding:1.25rem;
    font-size: 1.75rem;
    font-family: 'Quicksand';
    font-weight: 500;
    text-align: center;
}
section{
    padding: 1.5rem 3rem;
}
section h2{
    color: #424242;
    font-size: 1.25rem;
    font-weight: 700;
    font-family: 'Quicksand';
}
section .box{
    width:100%;
    background: #fff;
    box-shadow: 0 0 10px 1px rgba(0,0,0,0.1);
    padding: 0.5rem 1.5rem;
    margin-top: 1rem;
    border-radius:.25rem;
}

.input-group{
    margin: 1.25rem 0;
}
.input-group label{
    display: block;
    font-size: 1rem;
    color: #666666;
    text-align: left;
}
.input-group input{
    display: block;
    width:100%;
    padding:0.5rem;
    margin-top: 0.5rem;
    background-color: transparent;
    outline: none;
    font-size: 0.8rem;
    border-radius: 0.25rem;
    border: 1px solid #cccccc;
    font-family: 'Quicksand';
    font-weight: 600;
    letter-spacing: 1.5px;
}
.reset-button{
    display: flex;
}
.reset-button button{
    margin-left: auto;
    padding: 0.5rem 1rem;
    background-color: #212121;
    color: #fff;
    border: none;
    border-radius: 0.15rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: 0.75rem;
    font-family: 'Quicksand';
    font-weight: 600;
    cursor: pointer;
    outline: none;
}
.input-group textarea{
    width: 100%;
    max-width: 100%;
    max-height: 150px;
    margin-top: 0.75rem;
    display: block;
    padding: 0.5rem;
    font-size: 0.8rem;
    font-family: 'Quicksand';
    font-weight: 600;
    background-color: transparent;
    outline: none;
    border: 1px solid #cccccc;
    resize: none;
}
.one-rem-space{
    height: 1rem;
}
.half-rem-space{
    height: .5rem;
}
